import React, { useState, useEffect } from "react";
import style from "./index.module.scss";

import { useNavigate } from "react-router-dom";
import clsx from "clsx";
import { Swiper, SwiperSlide } from "swiper/react";
import { Navigation } from "swiper";
import { format } from "date-fns";
import TextTruncate from "react-text-truncate";
import { useWindowResize } from "beautiful-react-hooks";

import WithPadding from "@components/WithPadding";
import SectionHeader from "@components/SectionHeader";
import InlineLoader from "@components/InlineLoader";

import useBlogs from "./useBlogs";
import useGallery from "./useGallery";

import { splitArrChunks } from "@utils/helpers";

export default function Blog() {
  const navigate = useNavigate();

  const { blogsAll, blogPairs, featuredBlog, isLoading: isLoadingBlogs } = useBlogs();
  const { gallery, isLoading: isLoadingGallery } = useGallery();

  const [blogChunks, setBlogChunks] = useState([]);
  const [rowSize, setRowSize] = useState(3);

  function updateRowSize() {
    if (window.innerWidth < 1350 && window.innerWidth > 1050) {
      setRowSize(2);
    } else if (window.innerWidth < 1050 && window.innerWidth > 650) {
      setRowSize(1);
    } else if (window.innerWidth < 650) {
      setRowSize(1);
    } else {
      setRowSize(3);
    }
  }

  useWindowResize(updateRowSize);
  useEffect(() => {
    updateRowSize();
  }, []);

  useEffect(() => {
    setBlogChunks(splitArrChunks(blogsAll, rowSize));
  }, [blogsAll, rowSize]);

  return (
    <main className={style.wrapper}>
      {isLoadingBlogs || isLoadingGallery ? (
        <InlineLoader height="90vh" />
      ) : (
        <>
          {featuredBlog.published_date && (
            <section className={style.banner}>
              <WithPadding>
                <div className={style.bannerCard} onClick={() => navigate(`/blog/${featuredBlog.id}`)}>
                  <div className={style.image}>
                    <img src={featuredBlog.photo} alt="" />
                  </div>
                  <div className={style.blogContent}>
                    <div className={style.title}>{featuredBlog.title}</div>
                    <div className={style.date}>{format(new Date(featuredBlog.published_date), "dd MMM, yyyy")}</div>

                    <TextTruncate
                      className={style.body}
                      line={3}
                      element="div"
                      truncateText=".."
                      text={featuredBlog.summary}
                    />

                    <footer>
                      <div className={style.userImg}>
                        <img src={featuredBlog.author.photo} alt="" />
                      </div>
                      <div className={style.userInfo}>
                        <div className={style.name}>{featuredBlog.author.name}</div>
                        <div className={style.occupation}>{featuredBlog.author.designation}</div>
                      </div>
                    </footer>
                  </div>
                </div>
              </WithPadding>
            </section>
          )}

          <WithPadding>
            <div className={style.blogsSection}>
              <section className={style.blogs}>
                {blogChunks.map((blogPair, i) => (
                  <div className={style.blogRow} key={`row_${i}`}>
                    {blogPair.map((blog) => (
                      <article onClick={() => navigate(`/blog/${blog.id}`)} key={blog.id}>
                        <div className={style.image}>
                          <img src={blog.photo} alt="" />
                        </div>
                        <div className={style.blogContent}>
                          <div className={style.date}>{format(new Date(blog.published_date), "dd MMM, yyyy")}</div>
                          <div className={style.title}>{blog.title}</div>

                          <TextTruncate
                            className={style.body}
                            line={2.5}
                            element="div"
                            truncateText="…"
                            text={blog.summary}
                            textTruncateChild={<></>}
                          />
                        </div>
                      </article>
                    ))}
                  </div>
                ))}
              </section>
            </div>
          </WithPadding>

          <WithPadding className={style.gallerySection} id="blog_gallery_section">
            <header>
              <SectionHeader>Gallery</SectionHeader>
            </header>

            <Swiper
              className={style.gallery}
              slidesPerView={1}
              allowTouchMove={true}
              speed={1000}
              modules={[Navigation]}
              navigation
            >
              {gallery.map((item) => (
                <SwiperSlide key={item.id}>
                  {({ isActive }) => (
                    <div className={clsx(style.slide, isActive && style.active)}>
                      <img src={item.image} alt="" />
                      <div className={style.title}>{item.title}</div>
                    </div>
                  )}
                </SwiperSlide>
              ))}
            </Swiper>
          </WithPadding>
        </>
      )}
    </main>
  );
}
