import { useState, useEffect } from "react";

import { getBlogs } from "@apis/blogs";
import { splitArrChunks } from "@utils/helpers";

export default function useBlogs() {
  const [isLoading, setIsLoading] = useState(false);

  const [blogsAll, setBlogsAll] = useState([]);
  const [blogPairs, setBlogPairs] = useState([]);
  const [featuredBlog, setFeaturedBlog] = useState({});

  function populateBlogs() {
    setIsLoading(true);
    getBlogs()
      .then((resp) => {
        setBlogsAll(resp.data);
        const featured = resp.data.find((f) => f.is_featured);
        setFeaturedBlog(featured);
      })
      .catch((err) => {
        console.log(err);
      })
      .finally(() => {
        setIsLoading(false);
      });
  }

  useEffect(() => {
    // const blogsInPair = blogsAll.reduce((accum, curr, index, array) => {
    //   if (index % 3 === 0) accum.push(array.slice(index, index + 3));
    //   return accum;
    // }, []);

    setBlogPairs(splitArrChunks(blogsAll, 2));
  }, [blogsAll]);

  useEffect(() => {
    populateBlogs();
  }, []);

  return { blogsAll, blogPairs, featuredBlog, isLoading };
}
